.tooltipleft {
  position: relative;
  display: inline-block;
  float: right;
}

.tooltipleft .tooltiptextleft {
  visibility: hidden;
  width: 120px;
  background-color: #3f4d67;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;

  /* Position the tooltip */
  position: absolute;
  top: -5px;
  right: 105%;
  z-index: 1;
}

.tooltipleft:hover .tooltiptextleft {
  visibility: visible;
}

.tooltipright {
  position: relative;
  display: inline-block;
}

.tooltipright .tooltiptextright {
  visibility: hidden;
  width: 110px;
  background-color: #3f4d67;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 5px 2px;
  font-size: 15px;
  white-space: pre-line;


  /* Position the tooltip */
  position: absolute;
  top: -5px;
  left: 125%;
  z-index: 1;
}

.tooltipright:hover .tooltiptextright {
  visibility: visible;
}

.tooltipdown {
  position: relative;
  display: inline-block;
  float: right;
  margin-right: 10px;
  margin-left: 5px;
  margin-top: 5px;
  padding-bottom: 5px;
}

.tooltipdown .tooltiptextdown {
  visibility: hidden;
  width: 80px;
  background-color: #3f4d67;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 5px 5px;
  font-size: 13px;


  /* Position the tooltip */
  position: absolute;
  top: 100%;
  left: 50%;
  margin-left: -155px; /* Use half of the width (120/2 = 60), to center the tooltip */
  z-index: 1;
}

.tooltipdown:hover .tooltiptextdown {
  visibility: visible;
}

.tooltipup {
  position: relative;
  display: inline-block;
  float: right;
  margin-right: 10px;
  margin-left: 5px;
  margin-top: 5px;
  padding-bottom: 5px;
}

.tooltipup .tooltiptextup {
  visibility: hidden;
  width: 80px;
  background-color: #3f4d67;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 5px 5px;
  font-size: 15px;


  /* Position the tooltip */
  position: absolute;
  z-index: 1;
  bottom: 110%;
  left: 50%;
  margin-left: -40px;
}

.tooltipup:hover .tooltiptextup {
  visibility: visible;
}

